import { Type, I18n, Form } from 'rcfm'
import getProp from '../base/get-prop'



I18n.global.define({
  en: { formConditionEditor: {
    elseBranch: { resultTitle: 'Else' },
    whenBranch: {
      title: 'When',
      resultTitle: 'Then',
      delete: 'Delete Branch',
      add: 'New Branch',
      logic: { and: 'And', or: 'Or' },
    },
  } },
  zh: { formConditionEditor: {
    elseBranch: { resultTitle: '否则' },
    whenBranch: {
      title: '当',
      resultTitle: '则',
      delete: '删除分支',
      add: '新增分支',
      logic: { and: '且', or: '或' },
    },
  } },
})



function mutateBranch(mutation = {}, branches = {}, defaultResult) {
  let elseBranch = { ...branches.else }
  let whenBranches = branches.whens.slice()
  let { target, action, branchIndex, result } = mutation
  if (target === 'else') {
    if ('result' in mutation) { elseBranch.result = result }
  } else if (target === 'when') {
    if (action === 'delete') {
      whenBranches.splice(branchIndex, 1)
    } else if (action === 'append') {
      whenBranches.splice(branchIndex + 1, 0, {
        rules: [{ logic: '', expression: {} }],
        result: genResult(defaultResult),
      })
    } else if (action === 'update') {
      if ('result' in mutation) { whenBranches[branchIndex].result = result }
    }
  }
  return { whens: whenBranches, else: elseBranch }
}

function mutateRule(mutation = {}, branches = {}) {
  let elseBranch = { ...branches.else }
  let whenBranches = branches.whens.slice()
  let { action, branchIndex, ruleIndex, logic, name, rule, value } = mutation
  let rules = whenBranches.slice()[branchIndex].rules
  if (action === 'delete') {
    rules.splice(ruleIndex, 1)
    ruleIndex === 0 && rules.length && (rules[0].logic = '')
  } else if (action === 'append') {
    logic = ruleIndex === -1 ? '' : 'and'
    rules.splice(ruleIndex + 1, 0, { logic, expression: {} })
  } else if (action === 'update') {
    if ('logic' in mutation) { rules[ruleIndex].logic = logic }
    if ('rule' in mutation) { rules[ruleIndex].expression.rule = rule }
    if ('name' in mutation) {
      rules[ruleIndex].expression.name = name
      rules[ruleIndex].expression.rule = undefined
    }
    if ('value' in mutation) {
      rules[ruleIndex].expression.value = value
      rules[ruleIndex].expression.name = undefined
      rules[ruleIndex].expression.rule = undefined
    }
  }
  return { whens: whenBranches, else: elseBranch }
}

function genResult(defaultResult) {
  return Type.is.function(defaultResult) ? defaultResult() : defaultResult
}

function extractCondition(reactive, condition, defaultResult) {
  const Condition = reactive.schema.constructor.Condition
  let branches = Condition.extract(condition)
  if (Type.is.blank(branches.else.result)) {
    branches.else = { result: genResult(defaultResult) }
  }
  if (Type.isnt.array(branches.whens) || !branches.whens.length) {
    branches.whens = [{
      rules: [{ logic: '', expression: {} }],
      result: genResult(defaultResult),
    }]
  }
  branches.whens = branches.whens.map(({ rules, result }) => {
    rules = rules.length ? rules.map(({ logic, expression }) => ({
      logic, expression: reactive.validator.extractValidation(expression),
    })) : [{ logic: '', expression: {} }]
    return { rules, result }
  })
  return branches
}

function combineCondition(reactive, branches = {}) {
  const Condition = reactive.schema.constructor.Condition
  return Condition.combine({
    else: branches.else,
    whens: branches.whens.map(({ rules, result }) => {
      rules = rules.map(({ logic, expression }) => ({
        logic, expression: reactive.validator.combineValidation(expression),
      }))
      return { rules, result }
    }),
  })
}

function getProps(props = {}) {
  let reactive = getProp(props, 'reactive', { required: true })
  let member = getProp(props, 'member', { required: true })
  let onChange = getProp(props, 'onChange', { required: true })
  let ResultComponent = getProp(props, 'ResultComponent', { required: true })
  Type.mustbe.function(ResultComponent)
  let defaultResult = getProp(props, 'defaultResult', { default: undefined })
  let value = getProp(props, 'value', { default: [] })
  let branches = extractCondition(reactive, value, defaultResult)
  return { reactive, member, branches, ResultComponent, onChange(mu = {}) {
    let mutate = mu.target === 'rule' ? mutateRule : mutateBranch
    branches = mutate(mu, branches, defaultResult)
    value = combineCondition(reactive, branches)
    onChange(value)
  } }
}

function getLogics() {
  return [{
    value: 'and',
    label: I18n.global.t('formConditionEditor.whenBranch.logic.and'),
  }, {
    value: 'or',
    label: I18n.global.t('formConditionEditor.whenBranch.logic.or'),
  }]
}

function joinProvider(provider = {}) {
  if (!provider || !provider.id || !provider.prop) { return '' }
  return `${provider.id}/${provider.prop}`
}

function splitProvider(provider = '') {
  let [id, prop] = provider.split('/')
  return { id: id || '', prop: prop || '' }
}

function getProviders(props = {}) {
  let reactive = getProp(props, 'reactive', { required: true })
  let member = getProp(props, 'member', { required: true })
  return (Form.providers(reactive, member) || []).map((p) => ({
    value: joinProvider(p), label: reactive.get({ id: p.id, prop: 'label' }),
  }))
}

const Helper = {
  getProps,
  getLogics,
  getProviders,
  splitProvider,
  joinProvider,
}



export default Helper
