import { Type, Exception } from 'rcfm'
import Installer from './installer'
import './use'



Installer.prototype.try = function(names = []) {
  Type.isnt.array(names) && (names = [names])
  let matched = null
  if (!names.length) { return matched = null }
  names.some((name) => {
    try {
      matched = this.use(name)
      return true
    } catch (e) {
      matched = null
      return false
    }
  })
  if (matched) { return matched }
  let msg = `None of Component/Section '${JSON.stringify(names)}' is installed.`
  Exception.raise({ type: Installer.exceptions.NOT_FOUND, message: msg })
}
