import { Type, Struct, I18n, Form } from 'rcfm'
import getProp from '../base/get-prop'



function mutate(reactive, value = [], mutation = {}) {
  value = value.slice()
  let { index, action, name, rule } = mutation
  if (action === 'delete') {
    value.splice(index, 1)
  } else if (action === 'append') {
    value.splice(index + 1, 0, {})
  } else if (action === 'update') {
    if ('rule' in mutation) { value[index].rule = rule }
    if ('name' in mutation) {
      value[index].name = name
      value[index].rule = undefined
    }
  }
  return value.map((v) => {
    v = reactive.validator.combineValidation(v)
    return Struct.object.exclude(v, ['valOf'])
  })
}

function getProps(props = {}) {
  let reactive = getProp(props, 'reactive', { required: true })
  let member = getProp(props, 'member', { required: true })
  let onChange = getProp(props, 'onChange', { required: true })
  let value = getProp(props, 'value', { default: [] })
  Type.mustbe.array(value)
  if (!value.length) { value = [{}] }
  value = value.map((v) => reactive.validator.extractValidation(v))
  let actions = getProp(props, 'actions', {
    default: ['append', 'update', 'delete'],
  })
  Type.mustbe.array(actions)
  return { reactive, member, value, actions, onChange(mu) {
    onChange(value = mutate(reactive, value, mu))
  } }
}

function getValidators(props = {}) {
  let reactive = getProp(props, 'reactive', { required: true })
  let member = getProp(props, 'member', { required: true })
  return (Form.validators(reactive, member) || []).map((v) => {
    let label = I18n.global.t(`validator.validators.${v.name}.label`)
    return { label, value: v.name }
  })
}

function getRuleType(props = {}) {
  let reactive = getProp(props, 'reactive', { required: true })
  let member = getProp(props, 'member', { required: true })
  let value = getProp(props, 'value', { required: true })
  Type.mustbe.object(value)
  let { name, rule } = value
  let { ruleType } = reactive.validator.get(name) || {}
  if (!ruleType) { return undefined }
  if (Type.is.string(ruleType) && ruleType !== '*') { return ruleType }
  let validations = reactive.schema.for(member).validation || []
  let valueType = validations.find((v) => 'dataType' in v)?.dataType
  if (Type.is.string(valueType) && valueType !== '*') { return valueType }
  return 'String'
}

const Helper = { getProps, getValidators, getRuleType }



export default Helper
