import { Type, Struct } from 'rcfm'
import getProp from '../base/get-prop'
import joinClass from '../base/join-class'



function getProps(props = {}) {
  let type = getProp(props, 'type', { default: 'info' })
  let dismiss = getProp(props, 'dismiss', { default: Infinity })
  Type.mustbe.number(dismiss)
  let className = joinClass(`rcfm-alert-${type}`, props.className)
  return {
    className, dismiss,
    ...Struct.object.exclude(props, ['className', 'type', 'dismiss']),
  }
}



const Title = (() => {

  function getProps(props = {}) {
    let className = joinClass('rcfm-alert-title', props.className)
    return { ...props, className }
  }

  return { getProps }

})()



const Text = (() => {

  function getProps(props = {}) {
    let className = joinClass('rcfm-alert-text', props.className)
    return { ...props, className }
  }

  return { getProps }

})()



const Helper = {
  Title,
  Text,
  getProps,
}



export default Helper
