import { Type, Struct } from 'rcfm'
import getProp from '../base/get-prop'
import joinClass from '../base/join-class'



// [TODO] Refactor the mess

function excludeInputBuiltinProps(props = {}) {
  return Struct.object.exclude(props, [
    'className', 'invalid', 'size', 'name', 'placeholder',
    'disabled', 'readonly', 'debounce', 'onChange',
  ])
}

function getInputWrapperProps(props = {}, options = {}) {
  let mode = options.mode || 'loose'
  let overrides = options.overrides || {}
  let rootClass = 'rootClass' in options ? options.rootClass : 'rcfm-fminput'
  let size = getProp(props, 'size')
  let invalid = 'invalid' in props && props.invalid !== false
  let className = joinClass(props.className, [
    'rcfm-fmtag', rootClass,
    size ? `rcfm-fm-${size}` : '',
    invalid ? 'rcfm-fm-invalid' : '',
  ])
  let wrapperProps = { className, ...overrides }
  if (mode !== 'loose') { return wrapperProps }
  return { ...excludeInputBuiltinProps(props), ...wrapperProps }
}

function getInputProps(props = {}, options = {}) {
  let mode = options.mode || 'loose'
  let overrides = options.overrides || {}
  let name = getProp(props, 'name', { default: '' })
  let placeholder = getProp(props, 'placeholder', { default: '' })
  let disabled = 'disabled' in props && props.disabled !== false
  let readOnly = 'readonly' in props && props.readonly !== false
  let debounce = getProp(props, 'debounce', { default: null })
  let onChange = getProp(props, 'onChange', { default: null })
  let className = 'rcfm-fmtag'
  if (disabled || readOnly) { onChange = null }
  let inputProps = {
    className, name, placeholder, disabled,
    readOnly, debounce, onChange, ...overrides,
  }
  if (mode !== 'loose') { return inputProps }
  return { ...excludeInputBuiltinProps(props), ...inputProps }
}



const TextInput = (() => {

  function getProps(props = {}) {
    let multiline = 'multiline' in props && props.multiline !== false
    let inputProps = getInputProps(props, {
      mode: 'loose', overrides: { type: 'text', value: props.value || '' },
    })
    delete inputProps.multiline
    multiline && delete inputProps.type
    let wrapperProps = getInputWrapperProps(props, {
      mode: 'strict',
      rootClass: multiline ? 'rcfm-fmtextarea' : 'rcfm-fminput',
    })
    return { multiline, inputProps, wrapperProps }
  }

  return { getProps }

})()



const NumberInput = (() => {

  function getProps(props = {}) {
    let wrapperProps = getInputWrapperProps(props, {
      mode: 'strict', rootClass: 'rcfm-fminput',
    })
    let inputProps = getInputProps(props, { mode: 'loose', overrides: {
      type: 'number', value: (() => {
        if (isNaN(props.value)) { return '' }
        return typeof props.value === 'number' ? props.value : ''
      })(),
    } })
    return { wrapperProps, inputProps }
  }

  return { getProps }

})()



const Radios = (() => {

  function getProps(props = {}) {
    let wrapperProps = getInputWrapperProps(props, {
      mode: 'loose', rootClass: 'rcfm-fmradio',
    })
    delete wrapperProps.value
    delete wrapperProps.options
    let inputProps = getInputProps(props, {
      mode: 'strict', overrides: { type: 'radio', },
    })
    inputProps.readOnly && (inputProps.disabled = true)
    delete inputProps.debounce
    delete inputProps.placeholder
    let value = getProp(props, 'value', { default: undefined })
    let options = getProp(props, 'options', { default: [] })
    Type.mustbe.array(options)
    options.forEach((option) => {
      Type.mustbe.object(option, 'Radio option')
      Type.mustbe.string(option.label, 'Radio Option label')
    })
    return { inputProps, wrapperProps, value, options }
  }

  return { getProps }

})()



const Checkboxes = (() => {

  function getProps(props = {}) {
    let wrapperProps = getInputWrapperProps(props, {
      mode: 'loose', rootClass: 'rcfm-fmcheck',
    })
    delete wrapperProps.value
    delete wrapperProps.options
    let inputProps = getInputProps(props, {
      mode: 'strict', overrides: { type: 'checkbox' },
    })
    inputProps.readOnly && (inputProps.disabled = true)
    delete inputProps.debounce
    delete inputProps.placeholder
    let value = getProp(props, 'value', { default: [] })
    Type.mustbe.array(value)
    let options = getProp(props, 'options', { default: [] })
    Type.mustbe.array(options)
    options.forEach((option) => {
      Type.mustbe.object(option, 'Checkbox option')
      Type.mustbe.string(option.label, 'Checkbox label')
    })
    return { wrapperProps, inputProps, value, options }
  }

  function getNewValue(event, value = [], toggledValue) {
    let newValue = value.slice()
    let isIncluded = newValue.includes(toggledValue)
    if (event.target.checked) {
      !isIncluded && newValue.push(toggledValue)
    } else {
      isIncluded && (newValue = newValue.filter((v) => v !== toggledValue))
    }
    return newValue
  }

  return { getProps, getNewValue }

})()



const SingleSelect = (() => {

  function getProps(props = {}) {
    let wrapperProps = getInputWrapperProps(props, {
      mode: 'strict', rootClass: 'rcfm-fmselect',
    })
    let inputProps = getInputProps(props, { mode: 'loose' })
    inputProps.readOnly && (inputProps.disabled = true)
    let value = getProp(props, 'value', { default: undefined })
    let includeBlank = 'includeBlank' in props && props.includeBlank !== false
    let options = getProp(props, 'options', { default: [] })
    Type.mustbe.array(options)
    options.forEach((option) => {
      Type.mustbe.object(option, 'Select option')
      Type.mustbe.string(option.label, 'Select label')
    })
    delete inputProps.debounce
    delete inputProps.options
    delete inputProps.includeBlank
    delete inputProps.value
    return { inputProps, wrapperProps, includeBlank, options, value }
  }

  function findLabel(value, options = []) {
    return options.find((o) => o.value === value)?.label || ''
  }

  return { getProps, findLabel }

})()



const MultipleSelect = (() => {

  function getProps(props = {}) {
    let wrapperProps = getInputWrapperProps(props, {
      mode: 'strict', rootClass: 'rcfm-fmselect',
    })
    let inputProps = getInputProps(props, {
      mode: 'loose', overrides: { multiple: true },
    })
    inputProps.readOnly && (inputProps.disabled = true)
    let value = getProp(props, 'value', { default: [] })
    Type.mustbe.array(value)
    let options = getProp(props, 'options', { default: [] })
    Type.mustbe.array(options)
    options.forEach((option) => {
      Type.mustbe.object(option, 'Select option')
      Type.mustbe.string(option.label, 'Select label')
    })
    delete inputProps.placeholder
    delete inputProps.debounce
    delete inputProps.options
    delete inputProps.value
    return { inputProps, wrapperProps, options, value }
  }

  return { getProps }

})()



const Field = (() => {

  function getWrapperProps(props = {}) {
    let size = getProp(props, 'size', { default: '' })
    let className = joinClass(props.className, [
      'rcfm-fmtag', 'rcfm-fmfield', size ? `rcfm-fmfield-${size}` : '',
    ])
    return {
      ...Struct.object.exclude(props, [
        'className', 'size', 'label', 'hint', 'error',
      ]), className,
    }
  }

  function getLabelProps(props = {}) {
    let label = getProp(props, 'label', { default: '' })
    if (!label) { return {} }
    let size = getProp(props, 'size', { default: '' })
    let className = joinClass([
      'rcfm-fmtag', 'rcfm-fmlabel', size ? `rcfm-fmlabel-${size}` : '',
    ])
    return { className, label }
  }

  function getHintProps(props = {}) {
   let hint = getProp(props, 'hint', { default: '' })
    if (!hint) { return {} }
    let size = getProp(props, 'size', { default: '' })
    let className = joinClass([
      'rcfm-fmtag', 'rcfm-fmhint', size ? `rcfm-fmhint-${size}` : '',
    ])
    return { className, hint }
  }

  function getErrorProps(props = {}) {
    let error = (() => {
      if (!('error' in props)) { return '' }
      let msgs = Type.is.array(props.error) ? props.error : [props.error]
      return msgs.map((m) => m.message || m.name || m)
    })()
    if (!error) { return {} }
    let size = getProp(props, 'size', { default: '' })
    let className = joinClass([
      'rcfm-fmtag', 'rcfm-fmerror', size ? `rcfm-fmerror-${size}` : '',
    ])
    return { className, error }
  }

  function getProps(props = {}) {
    return {
      wrapperProps: getWrapperProps(props),
      labelProps: getLabelProps(props),
      hintProps: getHintProps(props),
      errorProps: getErrorProps(props),
    }
  }

  return { getProps }

})()



const Helper = {
  Field,
  TextInput,
  NumberInput,
  Radios,
  Checkboxes,
  SingleSelect,
  MultipleSelect,
}



export default Helper
