import { Type, I18n } from 'rcfm'
import getProp from '../base/get-prop'
import joinClass from '../base/join-class'



const StringEditor = (() => {

  function getProps(props = {}) {
    let value = getProp(props, 'value', { default: '' })
    let onChange = getProp(props, 'onChange')
    return { ...props, value, onChange }
  }

  return { getProps }

})()



const NumberEditor = (() => {

  function getProps(props = {}) {
    let value = getProp(props, 'value')
    let onChange = getProp(props, 'onChange')
    return { ...props, value, onChange }
  }

  return { getProps }

})()



const BooleanEditor = (() => {

  I18n.global.define('en.dataTypeEditor.boolean.option.true', 'Yes')
  I18n.global.define('en.dataTypeEditor.boolean.option.false', 'No')
  I18n.global.define('zh.dataTypeEditor.boolean.option.true', '是')
  I18n.global.define('zh.dataTypeEditor.boolean.option.false', '否')

  function getProps(props = {}) {
    let value = getProp(props, 'value', { default: undefined })
    let onChange = getProp(props, 'onChange')
    let options = [{
      value: true,
      label: I18n.global.t('dataTypeEditor.boolean.option.true'),
    }, {
      value: false,
      label: I18n.global.t('dataTypeEditor.boolean.option.false'),
    }]
    return { ...props, value, options, onChange }
  }

  return { getProps }

})()



const ObjectEditor = (() => {

  function stringify(value) {
    if ([null, undefined, NaN].includes(value)) { return '' }
    if (typeof value === 'string') { return value }
    try { return JSON.stringify(value) } catch (e) { return String(value) }
  }

  function parse(value) {
    if (typeof value !== 'string') { return value }
    if (value.match(/^\s*$/)) { return null }
    if (value === 'undefined') { return undefined }
    if (value === 'NaN') { return NaN }
    if (value.match(/-\s*NaN/)) { return NaN }
    if (value === 'Infinity') { return Infinity }
    if (value.match(/-\s*Infinity/)) { return -Infinity }
    try { return JSON.parse(value) } catch (e) { return value }
  }

  function getProps(props = {}) {
    let value = stringify(props.value)
    let onChange = (newVal) => props.onChange && props.onChange(parse(newVal))
    let className = joinClass('rcfm-object-type-editor', props.className)
  return { ...props, className, value, onChange }
  }

  return { getProps }

})()




const Helper = {
  String: StringEditor,
  Number: NumberEditor,
  Boolean: BooleanEditor,
  Object: ObjectEditor,
}



export default Helper
