import { Struct } from 'rcfm'
import getProp from '../base/get-prop'
import joinClass from '../base/join-class'



function getProps(props = {}) {
  let name = getProp(props, 'name', { required: true })
  let className = joinClass(props.className, `rcfm-ico-${name}`)
  return {
    ...Struct.object.exclude(props, ['name']), className,
  }
}



const Helper = { getProps }



export default Helper
