import { Type, Struct } from 'rcfm'
import getProp from '../base/get-prop'
import joinClass from '../base/join-class'



function getProps(props = {}) {
  let labels = getProp(props, 'labels', { required: true })
  Type.mustbe.array(labels)
  let activeIndex = getProp(props, 'activeIndex', { default: 0 })
  Type.mustbe.integer(activeIndex)
  let onSwitch = getProp(props, 'onSwitch', { default: null })
  onSwitch && Type.mustbe.function(onSwitch)
  activeIndex < 0 && (activeIndex = 0)
  activeIndex > labels.length - 1 && (activeIndex = labels.length - 1)
  let className = joinClass(props.className, 'rcfm-tabs')
  let classNames = {
    header: 'rcfm-tabs-header', title: 'rcfm-tabs-title',
    label: 'rcfm-tabs-label', pane: 'rcfm-tabs-pane',
  }
  return {
    ...Struct.object.exclude(props, [
      'className', 'labels', 'activeIndex', 'onSwitch',
    ]), className, classNames, labels, activeIndex, onSwitch,
  }
}

function activateClass(rootClass, index, activeIndex) {
  let active = index === activeIndex
  return `${rootClass} ${active ? rootClass + '-active' : ''}`
}



const Helper = { getProps, activateClass }



export default Helper
