import Debouncer from './debouncer'
import store from './helpers/store'
import './abort'



Debouncer.prototype.run = function(...args) {
  let { runner, interval } = store(this)
  this.abort(false)
  store(this).runnerArgs = args
  store(this).timer = setTimeout(() => {
    runner(...store(this).runnerArgs)
    store(this).runnerArgs = []
    store(this).timer = null
  }, interval)
}
