import { Context, Type } from 'rcfm'



function getReactive(props = {}) {
  Context.mustbeContext(props.reactive)
  return props.reactive
}

function getMember(props = {}) {
  Type.mustbe.object(props.member)
  Type.mustbe.nonblankString(props.member.id)
  return props.member
}

function getOnChange(props = {}) {
  Type.mustbe.function(props.onChange)
  return props.onChange
}



export default function getProp(props = {}, name = '', options = {}) {
  let required = 'required' in options ? options.required : false
  let defaultVal = 'default' in options ? options.default : undefined
  if (required) {
    Type.musntbe.blank(props[name], `Prop "${name}"`)
  } else {
    if (Type.is.blank(props[name])) { return defaultVal }
  }
  if (name === 'reactive') { return getReactive(props) }
  if (name === 'member') { return getMember(props) }
  if (name === 'onChange') { return getOnChange(props) }
  return Type.is.blank(props[name]) ? defaultVal : props[name]
}
