import { Type } from 'rcfm'
import store from './helpers/store'



class Debouncer {

  constructor(options = {}) {
    let runner = options.runner
    let interval = 'interval' in options ? options.interval : 100
    Type.mustbe.function(runner, 'Debouncer runner')
    Type.mustbe.positiveInteger(interval, 'Debouncer interval')
    store(this, { timer: null, interval, runner, runnerArgs: [] })
  }

  static create(...args) { return new Debouncer(...args) }

}



export { Debouncer, Debouncer as default }
