import Debouncer from './debouncer'
import store from './helpers/store'



Debouncer.prototype.abort = function(execute = true) {
  let { timer, runner } = store(this)
  if (timer === null) { return }
  clearTimeout(timer)
  execute && runner(...store(this).runnerArgs)
  store(this).runnerArgs = []
  store(this).timer = null
}
