import Installer from './installer'
import Debouncer from './debouncer'
import getProp from './base/get-prop'
import joinClass from './base/join-class'
import Alert from './components/alert'
import Button from './components/button'
import Icon from './components/icon'
import Tabs from './components/tabs'
import Form from './components/form'
import DataTypeEditor from './sections/data-type-editor'
import FormValidationEditor from './sections/form-validation-editor'
import FormConditionEditor from './sections/form-condition-editor'



function Helper() {
  let installer = Installer.create()
  // Helpers should be decorated on the installer instance.
  installer.Debouncer = Debouncer
  installer.getProp = getProp
  installer.joinClass = joinClass
  installer.Alert = Alert
  installer.Button = Button
  installer.Icon = Icon
  installer.Tabs = Tabs
  installer.Form = Form
  installer.DataTypeEditor = DataTypeEditor
  installer.FormValidationEditor = FormValidationEditor
  installer.FormConditionEditor = FormConditionEditor
  return installer
}



const UI = Helper()

if (typeof window !== 'undefined' && window.ReactiveForm) {
  window.ReactiveForm.UI = UI
}



export { UI }
